import React from 'react';
import { Modal } from 'antd';
import { YTopts } from './ytVideoOpts';
import YouTube from 'react-youtube';


const OnStartModal = ({ visible, closeModal }) => {

    const handleOnStateChange = (event) => {
        //close modal when video finished
        if (event.data === 0) closeModal()
    }

    return (
        <Modal
            title=""
            visible={visible}
            onCancel={closeModal}
            width={600}
            footer={null}
        >
            <h1>Trucada inesperada...</h1>
            <YouTube videoId={"MjXFP3kpdbA"} opts={YTopts} onStateChange={handleOnStateChange} />
        </Modal>
    )
}

export default OnStartModal;